@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Book.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Heavy.ttf);
  font-weight: 900;
}

body {
  font-weight: 300;
}

.form {
  position: relative;
  margin: 20px 0;
}

input::placeholder {
  color: rgba(61, 78, 97, 1);
  text-shadow: none;
}

.form-control-big {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  font-family: Avenir, sans-serif;
  padding: 23px 10px 23px 62px;
  font-size: 16px;
  line-height: 22px;
  text-shadow: 0 4px 4px rgba(0,0,0,0.25);
  box-shadow: 0 1px 8px rgba(61,78,97,0.1);
}

.form-control-big:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  font-weight: 900;
}

.form-control-big:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.form-control-label {
  position: relative;
}

.form-control-label::before {
  content: "";
  background: url("images/Search.svg");
  background-size: contain;
  position: absolute;
  display: inline-block;
  height: 19px;
  width: 19px;
  left: 26px;
  top: 25px;
}

.form-control-small {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  font-family: Avenir, sans-serif;
  padding: 10px 11px 10px 35px;
  font-size: 14px;
  line-height: 19px;
  text-shadow: 0 4px 4px rgba(0,0,0,0.25);
  box-shadow: 0 1px 8px rgba(61,78,97,0.1);
}

.d-block {
  width: 100%;
  display: block;
}

.form-control-small:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.form-small {
  position: relative;
}

.form-control-small:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  font-weight: 900;
}

.form-icon-small {
  position: relative;
}

.form-icon-small::before {
  content: "";
  background: url("images/Search.svg");
  background-size: contain;
  position: absolute;
  display: inline-block;
  height: 11px;
  width: 11px;
  left: 13px;
  top: 15px;
}
